import { Box, Stack, Typography } from "@mui/material";
import Controls from '../../../../components/controls/Controls'
import AddIcon from '@mui/icons-material/Add';
import { useState } from 'react'
import FormularioIncluirConsulta from "./FormularioIncluirConsulta";
import Tabela from "./Tabela";
import Filtros from "./Filtros";

export default function Agendamentos(props) {
    const [inserirConsulta, setInserirConsulta] = useState(false)

    const abrirInserirConsulta = () => {
        setInserirConsulta(true)
    }

    return (
        <Box>
            <Box sx={{ padding: '10px' }}>
                <Stack spacing={1} padding={1}>
                    <Controls.Button
                        sx={{ background: 'white' }}
                        text={
                            <Box sx={{ display: 'flex' }}>
                                <AddIcon />
                                <Typography> Consulta</Typography>
                            </Box>
                        }
                        size='large'
                        onClick={() => { abrirInserirConsulta() }}
                    />
                </Stack>
            </Box>
            
            <Filtros />

            <Box sx={{ overflowX: 'scroll' }}>
                <Tabela

                />
            </Box>
            {inserirConsulta && <FormularioIncluirConsulta />}
        </Box>
    )
}