import { Box, IconButton, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import * as Cores from '../../util/cores'

export default function CardMinhaArea(props) {
    const { icone, link, titulo } = props
    return (
        <Link to={link} style={{ textDecoration: 'none', color: Cores.COR_SECUNDARIA_ESCURO }}>
            <Box sx={{ background: 'white', borderRadius: '15px', width: '120px', height: '120px', margin: '10px' }}>
                <IconButton color="secondary" component="label">
                    {icone}
                </IconButton>
                <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} >{titulo}</Typography>
            </Box>
        </Link>
    )
}