import { Box, Divider, Typography } from "@mui/material";
import * as Cores from '../../../../util/cores';

const horas = ['01:00', '02:00', '03:00', '04:00', '05:00', '06:00', '07:00', '08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00',
    '19:00', '20:00', '21:00', '22:00', '23:00', '24:00']

export default function InformacoesDia(props) {
    const { isMobile, dataSelecionada, abrirDrawerInformacoes, horaSelecionada } = props

    return (
        <Box sx={{ background: Cores.COR_PRINCIPAL_FUNDO }}>
            <Divider />
            <Typography sx={{ padding: '5px' }} textAlign='left' fontSize={'large'} color={Cores.COR_SECUNDARIA}>Agenda Diria</Typography>
            <Box sx={{overflowY: 'scroll', height: '500px'}}>
                {horas.map((hora, index) => {
                    return (
                        <Box key={index} sx={{background: horaSelecionada == hora ? Cores.COR_SECUNDARIA : '', height: '17%'}} onClick={(e) => abrirDrawerInformacoes(e, hora)}>
                            <Divider />
                            <Typography sx={{padding: '4px'}} fontSize={'small'} textAlign={'left'} color={horaSelecionada == hora ? 'white' : Cores.COR_TEXTO_SEM_DESTAQUE}>{hora}</Typography>
                        </Box>
                    )
                })}
            </Box>
        </ Box>
    )
}