import { Box, Paper, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import Media from "react-media";
import secureLocalStorage from "react-secure-storage";
import Popup from "../components/Popup";
import Controls from "../components/controls/Controls";
import Footer from "../components/footer/Footer";
import FooterMobile from "../components/footer/FooterMobile";
import Mensagens from "../util/Mensagens";
import Utils from "../util/Utils";
import constantes from "../util/constantes";
import * as Cores from "../util/cores"
import AlterarSenha from "./alterar-senha/AlterarSenha";
import AtivarConta from "./ativar-conta/AtivarConta";
import Dashboard from "./dashboard/Dashboard";
import HomeWrapper from "./home/HomeWrapper";
import LoginWrapper from "./login/LoginWrapper";
import PerfilWrapper from "./perfil/PerfilWrapper";
import SobreWrapper from "./sobre/SobreWrapper";
import AgendaWrapper from "./consulta/AgendaWrapper";


export default function Wrapper(props) {
    const { corpo, isLoggedIn, userDetails, setUserDetails, setIsLoggedIn } = props
    const { height, width } = Utils.useWindowDimensions();
    const [paperWidth, setPaperWidth] = useState('')
    const [margin, setMargin] = useState('')
    const [mostrarPropaganda, setMostrarPropaganda] = useState(false);

    const isMobile = width < constantes.MOBILE_SCREEN_SIZE

    useEffect(() => {
        let mostrar = secureLocalStorage.getItem('mostrarPropaganda')?.valor
        setMostrarPropaganda(mostrar === undefined ? true : mostrar)
    }, []);

    const handleOkPropaganda = () => {
        secureLocalStorage.setItem('mostrarPropaganda', { valor: false })
        setMostrarPropaganda(false)
    }

    useEffect(() => {
        if (width < constantes.MOBILE_SCREEN_SIZE) {
            setPaperWidth('100%')
        } else {
            setPaperWidth('80%')
            setMargin('10%')
        }

    }, [])

    return (
        <Box sx={{
            width: paperWidth, marginInline: margin, marginTop: width < constantes.MOBILE_SCREEN_SIZE ? '10%' : '0px'
        }}>
             {corpo === 'agendamento' ?
                <AgendaWrapper
                    isLoggedIn={isLoggedIn}
                    userDetails={userDetails} 
                    isMobile={isMobile}/>
                :
                null
            }
            <Paper style={{
                background: Cores.COR_SECUNDARIA,
                display: ['perfil', 'sobre', 'dashboard'].includes(corpo) ? 'block' : 'none', height: height * 0.9, overflowY: 'scroll'
            }} >
                {corpo === 'perfil' ?
                    <PerfilWrapper
                        isLoggedIn={isLoggedIn}
                        userDetails={userDetails}
                        setUserDetails={setUserDetails} />
                    :
                    null
                }
                {corpo === 'sobre' ?
                    <SobreWrapper
                        isLoggedIn={isLoggedIn}
                        userDetails={userDetails} />
                    :
                    null
                }
                {corpo === 'dashboard' ?
                    <Dashboard
                        isLoggedIn={isLoggedIn}
                        userDetails={userDetails} />
                    :
                    null
                }
            </Paper>
            {corpo === 'home' ?
                <HomeWrapper
                    isLoggedIn={isLoggedIn}
                    userDetails={userDetails} />
                :
                null
            }
            {corpo === constantes.PAGE_ALTERAR_SENHA ?
                <AlterarSenha
                    isLoggedIn={isLoggedIn}
                    userDetails={userDetails} />
                :
                null
            }
            {corpo === constantes.PAGE_ATIVAR_CONTA ?
                <AtivarConta
                    isLoggedIn={isLoggedIn}
                    userDetails={userDetails} />
                :
                null
            }
            {corpo === 'login' ?
                <LoginWrapper
                    isLoggedIn={isLoggedIn}
                    userDetails={userDetails}
                    setIsLoggedIn={setIsLoggedIn}
                    setUserDetails={setUserDetails}
                />
                :
                null
            }


            {/* <Media queries={{ small: { maxWidth: constantes.MOBILE_SCREEN_SIZE } }}>
                {matches =>
                    matches.small ? (
                        <FooterMobile />
                    ) : (
                        <Footer />
                    )
                }
            </Media> */}

            <Popup
                maxWidth={'sm'}
                openPopup={mostrarPropaganda}
                setOpenPopup={setMostrarPropaganda}
                title={"Bem-vindo(a)"}
                width='550px'
            >
                <Box>
                    <Typography>
                        {Mensagens.PRIMEIRO_ACESSO_MENSAGEM}
                    </Typography>
                    <Controls.Button
                        sx={{ marginTop: '6%' }}
                        text='Ok!'
                        onClick={() => handleOkPropaganda()}
                    />
                </Box>
            </Popup>
        </Box>

    )
}