import { Container } from '@mui/material';
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import HeaderBar from '../components/header/HeaderBar';
import constantes from '../util/constantes';
import * as Cores from '../util/cores';
import Wrapper from "./Wrapper";

function Page(props) {
    const { corpo, isLoggedIn, userDetails, setUserDetails, setIsLoggedIn } = props
    const navigate = useNavigate();

    useEffect(() => {
        if ((userDetails === null || userDetails.id === 0) && corpo !== constantes.PAGE_ALTERAR_SENHA && corpo !== constantes.PAGE_ATIVAR_CONTA && corpo !== 'sobre'
            && corpo !== 'login' && corpo !== constantes.PAGE_CHECKOUT) {
            navigate('/', { replace: true });
        }
    }, []);

    return (
        <Container style={{
            position: 'absolute', top: 0, left: 0, minWidth: '100%', padding: '0px', height: '100%',
            zIndex: '-1',
            paddingTop: '5%',
            backgroundColor: Cores.COR_PRINCIPAL_CLARO
        }}>

            <HeaderBar
                corpo={corpo}
                setUserDetails={setUserDetails}
                isLoggedIn={isLoggedIn}
                userDetails={userDetails}
                setIsLoggedIn={setIsLoggedIn}
            />
            <Wrapper corpo={corpo}
                isLoggedIn={isLoggedIn}
                userDetails={userDetails}
                setUserDetails={setUserDetails}
                setIsLoggedIn={setIsLoggedIn}
            />
        </Container>
    )
}

export default Page;
