import { Box, Typography } from "@mui/material";
import * as Cores from '../../../../../util/cores';


export default function SelecionarDiaMesAno(props) {
    const {selecionarSemanaMesAno, periodo, semanaMesAnoSelecionado} = props

    return (
        <Box onClick={() => selecionarSemanaMesAno(periodo)} sx={{ borderRadius: '25px', 
            width: '100%', background: semanaMesAnoSelecionado===periodo ? Cores.COR_PRINCIPAL : Cores.COR_PRINCIPAL_FUNDO}}>
            <Typography>{periodo}</Typography>
        </Box>
    )
}