import { Box, Grid, Stack, Typography } from "@mui/material";
import { useState } from 'react'
import Controls from "../../components/controls/Controls";
import { Form, useForm } from "../../components/UseForm";
import Perfil from "./Perfil";
import ClienteService from "../../api/services/ClienteService";
import Mensagens from "../../util/Mensagens";
import Load from "../../components/Load";
import Notificacao from "../../components/Notificacao";
import * as Cores from "../../util/cores";
import ModalConfirmacao from "../../components/ModalConfirmacao";

const initialValues = {
    username: '',
    emailAlterarSenha: '',
    senha: ''

}

export default function GerenciarConta(props) {
    const { userDetails, setUserDetails } = props
    const [showLoad, setshowLoad] = useState(false)
    const [exibirModal, setExibirModal] = useState(false)
    const [notify, setNotify] = useState({
        isOpen: false,
        message: "",
        type: "",
    })

    const validate = (fieldValues = values) => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        handleInputChangeSwitch,
        erros,
        resetForm,
        setErros
    } = useForm(initialValues, true, validate)

    async function handleExcluir() {
        await ClienteService.deletarCliente(userDetails.id, setNotify, setshowLoad, setUserDetails, setExibirModal)
    }

    const handleClickExcluir = () => {
        setExibirModal(true)
    }

    async function handleAlterarSenha() {
        setshowLoad(true)
        try {
            await ClienteService.solicitaAlterarSenha({
                userTO: {
                    username: userDetails.username,
                    password: ''
                },
                id: userDetails.id,
                email: values.emailAlterarSenha
            }).then(response => {
                if (response.status !== 200) {
                    setNotify({
                        isOpen: true,
                        message: Mensagens.ERRO_SERVICO,
                        type: "error",
                    })
                } else {
                    setNotify({
                        isOpen: true,
                        message: response.mensagem,
                        type: response.erro ? "error" : "success",
                    })
                }
            })
        } catch (e) {

        } finally {
            setshowLoad(false)
        }
    }

    return (
        <Form>
            <Load exibirLoad={showLoad} mensagemLoad='Solicitando alterao de senha...' />
            <Notificacao notify={notify} setNotify={setNotify} />
            <Stack spacing={4}>
                <Perfil userDetails={userDetails} setUserDetails={setUserDetails} />
                <Box sx={{ textAlign: 'left' }}>
                    <Typography variant='h5' color={Cores.COR_PRINCIPAL}>Alterar Senha</Typography>
                    <Box style={{ borderBottom: '2px solid ' + Cores.COR_PRINCIPAL, width: '100%', marginBottom: '20px' }}></Box>
                    <Grid container spacing={3}>
                        <Grid item md={4} sm={6}>
                            <Controls.TextField
                                name='emailAlterarSenha'
                                label='Email'
                                value={values.emailAlterarSenha}
                                onChange={handleInputChange}
                                error={erros.emailAlterarSenha}
                            />
                        </Grid>
                        <Grid item md={4} sm={6}>
                            <Controls.Button
                                text='Alterar'
                                onClick={handleAlterarSenha}
                            />
                        </Grid>
                    </Grid>


                </Box>
                <Box sx={{ textAlign: 'left', marginTop: '30px' }}>
                    <Typography variant='h5' color={Cores.COR_PRINCIPAL}>Excluir Conta</Typography>
                    <Box style={{ borderBottom: '2px solid ' + Cores.COR_PRINCIPAL, width: '100%', marginBottom: '20px' }}></Box>
                    <Grid container spacing={3}>
                        <Grid item md={2} sm={4}>
                            <Controls.TextField
                                name='senha'
                                label='Senha'
                                value={values.senha}
                                onChange={handleInputChange}
                                error={erros.senha}
                            />
                        </Grid>
                        <Grid item md={2} sm={4}>
                            <Controls.Button
                                text='Excluir'
                                onClick={handleClickExcluir}
                                color='error'
                            />
                        </Grid>
                    </Grid>
                </Box>
            </Stack>
            <ModalConfirmacao
                exibirModal={exibirModal}
                setExibirModal={setExibirModal}
                handleConfirmar={handleExcluir}
                texto='Deseja mesmo excluir sua conta?'
            />
        </Form>
    )
}