import { Box, Typography } from "@mui/material";
import * as Cores from '../../../../util/cores'
import * as Constantes from '../../../../util/constantes'

export default function DataSelecionadaHeader(props) {

    const { alterarCalendarioAgendamento, calendarioAgendamento } = props

    return (
        <Box sx={{ background: 'white', padding: '2px', display: 'flex', justifyContent: 'space-around' }}>
            <Box sx={{ padding: '5px', borderRadius: '5px', width: '100%', background: calendarioAgendamento == Constantes.CALENDARIO ? Cores.COR_PRINCIPAL : 'white' }}
                onClick={() => alterarCalendarioAgendamento(Constantes.CALENDARIO)}>
                <Typography fontSize={'large'} color={calendarioAgendamento == Constantes.CALENDARIO ? 'white' : Cores.COR_SECUNDARIA}>
                    {Constantes.CALENDARIO}
                </Typography>

            </Box>
            <Box sx={{  padding: '5px', borderRadius: '5px', width: '100%', background: calendarioAgendamento == Constantes.AGENDAMENTO ? Cores.COR_PRINCIPAL : 'white' }}
                onClick={() => alterarCalendarioAgendamento(calendarioAgendamento == Constantes.AGENDAMENTO ? 'white' : Constantes.AGENDAMENTO)}>
                <Typography fontSize={'large'} color={ calendarioAgendamento == Constantes.AGENDAMENTO ? 'white' : Cores.COR_SECUNDARIA}>
                    {Constantes.AGENDAMENTO}
                </Typography>
            </Box>
        </Box>
    )
}